<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default {
  data () {
    const validateImg = (rule, value, callback) => {
      if(this.imgFileList.length>0) {
        callback();
      } else {
        this.$Message.warning('请选择图片')
      }
    };
    return {
      formValidate: {
        name: '',
      },
      ruleValidate: {
        name: [
          { required: true, message: '请输入游戏名称', trigger: 'blur' }
        ],
        img: [
          { validator: validateImg, trigger: 'blur' }
        ],
      },
      preImgList: [],
      imgFileList: [],
      keywordList: [],
      isSearchFGame: false
    }
  },
  created () {
    if(this.$route.query.gameid) {
      const token = getToken(),
            id = this.$route.query.gameid,
            type = 1,
            params=  {token, id, type};

      api.editImg(params).then(res => {
        if(res.code == 0) {
          this.formValidate.name = res.data.gameInfo.name
        } else {
          this.$Notice.error({
            desc: res.msg
          })
        }
      })
    }
  },
  methods: {
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          const token = getToken(),
                classify = 1,
                name = this.formValidate.name;

          const fr = new FormData();
                fr.append('token', token);
                fr.append('classify', classify);
                fr.append('name', name);
                fr.append('imgcount', this.imgFileList.length);
                
          this.imgFileList.forEach((ele,i) => {
            fr.set('img'+(i+1), ele);
          })
          const params = fr;
          this.$Spin.show();
          api.exeImg(params).then(res => {
            this.$Spin.hide();
            if(res.code == 0) {
              this.$Notice.success({title:'添加成功'})
              this.$router.push('/data/gameimg')
            } else {
              this.$Notice.error({desc: res.msg})
            }
          })     
        } else {
            this.$Message.error('Fail!');
        }
      })
    },
    autoKeyword (query) {
      if(query != '') {
        const token = getToken(),
            name = this.formValidate.name,
            params = {token, name};
         
        api.backGameList(params).then(res => {
          if(res.code == 0) {
            if(res.data.length>0) {
              this.keywordList = res.data
              this.isSearchFGame = true;
            } else {
              this.isSearchFGame = false;
            }
          }else {
            this.isSearchFGame = false;
            this.$Notice.error({
              desc: res.msg
            })
          }
        })
      }
    },
    checkGame () {
      this.isSearchFGame = false
    },//主库游戏名称校验
    setKeyword (name) {
      this.formValidate.name = name
    },//主库游戏名称选中
    getFile (e) {
      // console.log(e.target.files)
      const files = e.target.files;
      this.files = files
      // console.log(Object.keys(files), Object.values(files))
      if(Object.values(files).length>0) {
        Object.values(files).forEach(ele => {
          const file = ele,
                name = ele.name,
                type = file.type,
                limitType = 'jpg|jpeg|png',
                limitTypeReg = new RegExp('.('+limitType+')$');

          const isRepeat = this.imgFileList.some(ele1 => {
            return ele1.name == name
          })
          // console.log(isRepeat)
          if (!limitTypeReg.test(type)) {
            this.$Message.warning(`请传${limitType}图片`)
          } else if (isRepeat) {
            this.$Message.warning('图片已存在请勿重复选择')  
          } else {
            this.imgFileList.push(ele);
            let fr = new FileReader();
            fr.onload = () => {
              this.preImgList.push(fr.result)
            };
            fr.readAsDataURL(ele)
          }
        })
      } else {
        this.$Message.warning('您还没有选择图片')
      }
    },//img file获取
    handleRemoveGameImg (index) {
      this.preImgList.splice(index, 1)
      this.imgFileList.splice(index, 1)
      // console.log(this.preImgList, this.imgFileList)
    },//截图删除
  }
}
</script>
<template>
  <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
    <FormItem label="游戏名称" prop="name" class="small nameBox">
      <Input
        clearable
        v-model="formValidate.name"
        @on-change="autoKeyword"
        @on-blur="checkGame"
      />
      <ul v-show="isSearchFGame">
        <li 
          v-for="item,idx in keywordList" 
          :value="item" 
          :key="idx"
          @mousedown="setKeyword(item)"
          >{{ item }}</li>
      </ul>
      <!-- <Select clearable v-model="formValidate.name" filterable :remote-method="autoKeyword">
        <Option v-for="item,idx in keywordList" :value="item" :key="idx">{{ item }}</Option>
      </Select> -->
    </FormItem>
    <FormItem label="选择截图" prop="img" class="big iconBox">
      <div class="uploadBox">
        <div class="upload">
          <Icon type="ios-cloud-upload-outline" size=18></Icon>
          选择文件
        </div>
        <input type="file" name="img" value="" multiple="multiple" @change="getFile($event)">
      </div>
      <i>已选择{{preImgList.length}}张图片</i>
      <span style="color:orange;margin-left:20px">按 Ctrl 键可一次添加多张图片 </span>
    </FormItem>
    <FormItem label="截图预览">
      <div class="uploadlistBox" id="uploadListBox" v-if="preImgList.length>0">
        <div class="upload-list" v-for="item,idx in preImgList" :key="idx">
          <img :src="item" title="item.path">
          <div class="upload-list-cover" @click="handleRemoveGameImg(idx)">
            <Icon type="ios-trash-outline"></Icon>
          </div>
        </div>
      </div>
      <div class="noImg" v-else>
        <Icon type="ios-images-outline" size="52"></Icon>
        <p>暂无截图预览</p>
      </div>
    </FormItem>
    <FormItem>
      <Button type="primary" @click="handleSubmit('formValidate')">确认提交</Button>
    </FormItem>
  </Form>
</template>
<style lang="scss" scoped>
  .small {
    width: 20%;
    margin-bottom: 40px;
  }
  .big {
    width: 50%;
    margin-bottom: 30px;
  }
  .uploadBox {
      display: inline-block;
      width: 100px;
      height: 34px;
      border-radius: 4px;
      border: 1px dashed #ccc;
      position: relative;
      input {
        opacity: 0;
        width: 100px;
        height: 32px;
      }
      .upload {
        position: absolute;
        top: 0;
        left: 0;
        width: 100px;
        padding: 0 4px 0 12px;
      }
  }
  .uploadlistBox{
    background: #fff;
    border: 1px dashed #dcdee2;
    border-radius: 4px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    transition: border-color .2s ease;
    padding: 20px 20px 20px;
    &:hover {
      border: 1px dashed #2d8cf0;
    }
    .upload-list{
      display: inline-block;
      height: 144px;
      line-height: 144px;
      border-radius: 4px;
      overflow: hidden;
      position: relative;
      margin-right: 6px;
      text-align: center;
      box-shadow: 0 1px 1px #00000020;
      &:hover .upload-list-cover {
        display: block;
      }
      img{
        width: 100%;
        height: 100%;
      }
      .upload-list-cover{
        display: none;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba(0,0,0,.6);
        i{
          color: #fff;
          font-size: 20px;
          cursor: pointer;
          margin: 0 2px;
        }
      }
    }
    .uploadedIcon, .uploadedIconError {
      position: absolute;
      bottom: 2px;
      right: 6px;
      font-size: 26px;
      color: #fff;
      z-index: 1;
      &::after {
        content: '';
        position: absolute;
        top: -32px;
        left: -28px;
        border-width: 30px;
        border-style: solid;
        border-radius: 0 0 4px 0;
        border-color: transparent green green transparent;
        z-index: -1;
      }
    }
    .uploadedIconError {
      &::after {
        border-color: transparent red red transparent;
      }
    }
    .processBox {
      position: absolute;
      top: 0;
      left: 0;
      background: #0009;
      padding: 2px;
    }
  }
  .noImg {
    border: 1px dashed #ccc;
    padding: 20px 0px;
    border-radius: 3px;
    text-align: center;
    padding: 20px 0;
    i {
      color: #3399ff
    }
  }
  .nameBox {
    display: inline-block;
    position: relative;
    margin-right: 20px;
    ul {
      min-width: 180px;
      max-height: 200px;
      position: absolute;
      will-change: top, left;
      transform-origin: center top;
      top: 33px;
      left: 0px;
      list-style: none;
      overflow: auto;
      margin: 5px 0;
      padding: 5px 0;
      background-color: #fff;
      box-sizing: border-box;
      border-radius: 4px;
      box-shadow: 0 1px 6px #00000020;
      z-index: 900;
      li {
        width: 180px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        line-height: 32px;
        text-indent: 8px;
        margin: 0;
        clear: both;
        color: #515a6e;
        font-size: 12px!important;
        white-space: nowrap;
        list-style: none;
        cursor: pointer;
        transition: all .2s ease-in-out;
        &:hover {
          background: #dedede99;
        }
      }
    }
    span {
      width: 110px;
      position: absolute;
      color: green;
      margin-left: 6px;
      &.err2 {
        color: red;
      }
    }
    .addGame {
      position: absolute;
      margin-left: 100px;
    }
  }
</style>
